import React, {useEffect, useState} from "react";
import styled from "styled-components";
import Star from "../Star";

import {useWeb3} from "../../api/contracts";

import homeling from "../../assets/images/homeLing.png";
import EquipBg from "../../assets/images/equipBg.png";
import Bgbtn from "../../assets/images/btn.png";
import FrameBg from "../../assets/images/Frame.png";

import equiplist from "../../json/equip";
import ConfirmModal from "../modal/confirmModal";
import BgbtnNor from "../../assets/images/btnWhite.png";


const Ul = styled.ul`
  display: flex;
  flex-wrap: wrap;
  //height: 100%;
  margin-left: -0.4rem;
  li{
   width: 33.3333333333%;
   padding-left: 0.4rem;
   box-sizing: border-box;
   word-break: break-all;
   padding-top: 0.56rem;
  }
`;
const FirstRow = styled.div`
  display:flex;
  align-items: flex-end;
`;


const ImgBg = styled.div`
  background: url(${EquipBg}) no-repeat;
  background-size: 100% 100%;
  width: 0.8rem;
  height: 0.8rem;
  //margin:0 0.16rem 0.16rem 0;
  margin-right: 0.16rem;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
  width: 0.56rem;
  height: 0.56rem;
  }
`;
const Tits = styled.div`
  font-size: 0.16rem;
  font-family: "Orbitron-Medium";
  padding: 0.24rem 0  0.16rem;
`;

const Lv = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 0.08rem;
  span{
    font-size: 0.12rem;
    font-weight: 400;
    color: #FF9400;
    line-height: 0.24rem;
    margin-left: 0.08rem;
  }
`;


const RhtA = styled.a`
    width: 0.8rem;
    height:0.32rem;
    display: inline-block;
    background: url(${Bgbtn}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.32rem;
    font-family: "Orbitron-Medium";
    font-size: 0.12rem;
    text-align: center;
    font-weight: 500;
    color: #0C0C0B;
    text-transform: capitalize;
    cursor: pointer;
`;

const RhtBtn = styled(RhtA)`
   background: url(${BgbtnNor}) no-repeat;
    background-size: 100% 100%;
    color: #FFF9F2;
`


const StarVg = styled.div`
  img{
  width: 0.12rem;
  height: 0.12rem;
  margin-right: 0.08rem;
  }
`

const FirstBgTop = styled('div')`
  display: flex;
  justify-content: space-between;
  .frameimg{
    height: 1.24rem;
  }
`;

const SecondRw = styled('div')`
  display: flex;
  justify-content: space-between;
  align-items: center;
  
`

export default function Equiplist() {
    const {  dispatch } = useWeb3();
    const [activeItem, setActiveItem] = useState(null);
    const [tips, setTips] = useState(null);

    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: true });
    },[]);


    const buyAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Confirm the sale of your equipment .")
    }

    const CancelAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Are you sure you want to cancel the current equipment sale ?")
    }

    return<div>
        <ConfirmModal item={activeItem} tips={tips}  buttonTips="upload" />
        <Ul>
            {
                equiplist.map((item,index)=>( <li>
                    <FirstBgTop>
                        <div>
                            <FirstRow>
                                <ImgBg>
                                    <img src={item.img} alt=""/>
                                </ImgBg>
                                <div>
                                    <Lv>
                                        <img src={homeling} alt=""/>
                                        <span>{item.lv}</span>
                                    </Lv>
                                    <StarVg> <Star value={item.star}/></StarVg>
                                </div>
                            </FirstRow>
                            <Tits>{item.name}</Tits>

                        </div>
                        <img src={FrameBg} className="frameimg" alt=""/>
                    </FirstBgTop>

                    <SecondRw>
                        {
                            item.sale &&<RhtA onClick={()=>buyAircraft(item)}>Sale</RhtA>
                        }
                        {
                            !item.sale &&<RhtBtn onClick={()=>CancelAircraft(item)}>Cancel</RhtBtn>
                        }

                    </SecondRw>
                </li>))
            }

    </Ul>
    </div>
}
