import React from "react";
import styled from "styled-components";
import Modal from "./modal";



import progress from "../assets/images/modal/progress.png";
import close from "../assets/images/iconClose.png";
import soon from "../assets/images/mobile/soon.png";

import closebtn from "../assets/images/mobile/closebtn.png";
import Btn120 from "../assets/images/btn120.png";
import {useWeb3} from "../api/contracts";



const Titles = styled('div')`
    margin: 0.48rem 0 0.24rem;
    font-size: 0.48rem;
    font-family: 'orbitron';
    font-weight: bold;
    line-height: 0.56rem;
    background: linear-gradient(180deg, rgba(255, 148, 0, 0) 0%, #FFBC3C 0%, #FF9400 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
`
const BtnClose = styled('div')`
    background: url(${Btn120}) no-repeat;
    background-size: 100% 100%;
    width: 1.2rem;
    height: 0.44rem;
    margin: 0 auto;
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    color: #0C0C0B;
    line-height: 0.44rem;
    cursor: pointer;
`

const PcSoon = styled('div')`
  display: block;
   @media (max-width: 1000px) {
        display: none;
    }
`;

const MobileSoon = styled('div')`
  display: none;
  .title{
 

    font-family: 'orbitron';
    font-weight: bold;

    background: linear-gradient(180deg, rgba(255, 148, 0, 0) 0%, #FFBC3C 0%, #FF9400 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: capitalize;
    margin-top: 48px;
    font-size: 40px;
    line-height: 48px;

    }
    .btnclose{
        background: url(${closebtn}) no-repeat;
        background-size: 100% 100%;
        width: 100px;
        height: 44px;
        margin: 0 auto;
        font-size: 14px;
        font-family: 'orbitron-regular';
        font-weight: 400;
        color: #FF9500;
        line-height: 44px;
        cursor: pointer;
        margin-top: 24px;
    }
    img{
    //width: 350px;
    //height: 324px;
    max-width: 80%;
    }
  @media (max-width: 1000px) {
        display: block;
    }
    
`;

export default function Coming() {
    const {  dispatch } = useWeb3();

    const handleclose = () =>{
        dispatch({type: 'SHOW_MODAL',payload:null});
    }

    return       <Modal bg={true}>
        <PcSoon>
            <img src={progress} alt=""/>
            <Titles>Coming<br/> soon</Titles>
            <BtnClose onClick={()=>handleclose()} src={close} alt="">Close</BtnClose>
        </PcSoon>
        <MobileSoon>
            <img src={soon} alt=""/>
            <div className="title">Coming soon</div>
            <div className="btnclose" onClick={()=>handleclose()} src={close} alt="">Close</div>
        </MobileSoon>
    </Modal>
}
