import styled from 'styled-components';
import bg from '../../assets/images/progressbg.png'

import atk from '../../assets/images/icon/atk.png'
import hp from '../../assets/images/icon/hp.png'
import aspd from '../../assets/images/icon/aspd.png'

const Box = styled('div')`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin-bottom: 0.48rem;
`

const Bg = styled('div')`
  background: url(${bg}) no-repeat;
  background-size: 100% 100%;
  width: 0.48rem;
  height: 0.48rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.16rem;
  img{
    width: 0.28rem;
    height: 0.28rem;
  }
    @media (max-width: 1000px) {
      display: none;
    }
`
const BgRht = styled('div')`
  background: url(${bg}) no-repeat;
  background-size: 100% 100%;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  img{
    width: 20px;
    height: 20px;
  }
  @media (min-width: 1000px) {
      display: none;
    }
`

const Titles = styled('div')`
    font-size: 0.14rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: #FFFAF2;
    line-height: 0.24rem;
    text-transform: uppercase;
     @media (max-width: 1000px) {
      font-size: 14px;
    }
`;

const Rht = styled('div')`
    flex-grow: 1;
`

const ProgressWidth = styled('ul')`
    width: 100%;
    border: 1px solid;
    border-image: linear-gradient(180deg, rgba(255, 189, 61, 1), rgba(255, 149, 0, 1)) 1;
    box-sizing: border-box;
    padding: 0.04rem 0.07rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li{
      width: 18%!important;
      border: 1px solid;
      border-image: linear-gradient(180deg, rgba(255, 189, 61, 1), rgba(255, 149, 0, 1)) 1;
      height: 0.06rem;
  
    }
    .half{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
        span{
        display: inline-block;
        width: 50%;
        height: 100%;
        background: linear-gradient(180deg, rgba(255, 149, 0, 0) 0%, #FFBD3D 0%, #FF9500 100%);
        }
      
    }
    .active{
      background: linear-gradient(180deg, rgba(255, 149, 0, 0) 0%, #FFBD3D 0%, #FF9500 100%);
    }
    
    @media (max-width: 1000px) {
        height: 18px;
        padding: 4px 7px;
        box-sizing: border-box;
           li{
          height: 6px;
       }
    }
`
const Top = styled('div')`
margin-bottom: 16px;
   @media (max-width: 1000px) {
       display: flex;
       align-items: center;
       font-size: 14px;
    }
`

export default function Progress(props) {

    const ImgType = (type) =>{
        let img;
        switch (type) {
            case "atk":
                img = atk;
                break;
            case "hp":
                img = hp;
                break;
            case "aspd":
                img = aspd;
                break;
            default:break;
        }
        return img
    }
    return <Box>
        <Bg>
            <img src={ImgType(props.type)} alt=""/>
        </Bg>
        <Rht>
            <Top>
                <BgRht>
                    <img src={ImgType(props.type)} alt=""/>
                </BgRht>
                <Titles>{props.type}</Titles>
            </Top>

            <ProgressWidth>

                {[...Array( parseInt(props.value))].map((e, i) =>
                    <li  key={`all_${i}`} className="active" />
                )}
                {
                    props.value % 1 >0  &&    <li className="half" ><span /></li>
                }
                {[...Array( (5 - (props.value).toFixed(0)))].map((e, i) =>
                    <li  key={`empty_${i}`}/>
                )}

            </ProgressWidth>
        </Rht>
    </Box>
}
