import { ethers } from "ethers";
import abi from "../abi/Fighter";

let loadMain = false;
let Mock27Contract;

const { mockX27 } = window.GeneralConfig;

export default async function Mock27Connect(state, dispatch) {


    const {web3InstanceState, Mock27ContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            Mock27Contract = new ethers.Contract(mockX27, abi, web3Instance);
            dispatch({type: 'SET_MOCKX27', payload: Mock27Contract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MOCKX27_ERROR'});
        }
    };
    if (Mock27ContractState !== 'LOAD_MOCKX27') return;
    if (loadMain) return dispatch({type: 'SET_MOCKX27', payload: Mock27Contract});
    loadMain = true;
    asyncLoadMain();
}
