export default function MyAircraft() {
    const {dispatch,state} = useWeb3();
    const { Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract,Account} = state;

    const [activeItem, setActiveItem] = useState(null);
    const [tips, setTips] = useState(null);
    const [arrlist, setArrlist] = useState([]);

    if(window.ethereum){
        window.ethereum.on('accountsChanged', function (arr) {
            dispatch({type: 'SET_ALLACCOUNT',payload:arr[0]});
        });

    }else{
        console.log("===window.ethereum not found=====")
    }

    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: true });
    },[]);

    useEffect(() => {
        // dispatch({ type: 'SHOW_PAGE', payload: true });

        if(Mock29Contract == null || Account == null )return
        const getList = async (contract) =>{
            dispatch({type: 'SET_LOADING',payload:true});

           let totalfighter = await mockx.getBalance(contract,Account);
            console.log(totalfighter);
            const total = totalfighter && BigNumber.from(totalfighter).toNumber();

            if (!total) return
            const promiseArr = []
            for (let index = 0; index < total; index++) {
                const rs = mockx.tokenOfOwnerByIndex(contract,Account, index);
                promiseArr.push(rs)
            }
            const fighterIds = await Promise.all(promiseArr)
            const fighterArr = fighterIds.toString().split(",");
            dispatch({type: 'SET_LOADING',payload:null});

            return fighterArr
        }

        const init = async()=>{
            let arr = []
            let arr21 = await getList(Mock21Contract,21);

            if(arr21){
                [...Array(arr21.length)].map(item=> arr.push(myX21))
                // arr21.map(async (item)=>{
                //     let tokenURI = await mockx.tokenURI(contract,item);
                //     console.error("=====tokenURI====",tokenURI)

                // })
            }

            let arr24 = await getList(Mock24Contract,24)
            if(arr24){
                [...Array(arr24.length)].map(item=> arr.push(myX24))
            }

            let arr27 = await getList(Mock27Contract,27)

            if(arr27){
                [...Array(arr27.length)].map(item=>arr.push(myX27))
            }

            let arr29 = await getList(Mock29Contract,29)

            if(arr29){
                [...Array(arr29.length)].map(item=>arr.push(myX29))
            }
            let arrObj = FormatJson(arr);
            setArrlist(arrObj)
        }
        init()


    },[Mock29Contract,Account]);

    const FormatJson = (arrObj) =>{
        arrObj.map(item=>{
            let attr={}
            item.attributes.map(p=> attr[p.trait_type]=p.value)
            item.attr = attr;
            return item;
        })
        return arrObj

    }
    const SaleAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Confirm the sale of your aircraft .")
    }
    // const CancelAircraft = (item) =>{
    //     dispatch({type: 'SHOW_MODAL',payload: true});
    //     setActiveItem(item)
    //     setTips("Are you sure you want to cancel the current aircraft sale ?")
    // }

    return <Content>
        <ConfirmModal item={activeItem} tips={tips} buttonTips="upload" />
        <Ul>
            {
                arrlist.map((item,index)=>(
                    <li key={`my_${index}`}>
                        <Lilft>
                            <Imgs src={item.image} alt=""/>
                            <ImgsBg src={aircraftLft} alt=""/>
                            <RhtA onClick={()=>SaleAircraft(item)} disabled={true}>Sale</RhtA>
                            {/*{*/}
                            {/*    item.sale &&<RhtA onClick={()=>SaleAircraft(item)}>Sale</RhtA>*/}
                            {/*}*/}
                            {/*{*/}
                            {/*    !item.sale &&<RhtBtn onClick={()=>CancelAircraft(item)}>Cancel</RhtBtn>*/}
                            {/*}*/}
                        </Lilft>
                        <Lirht>
                            <Titles>{item.name}</Titles>

                            <div>
                                <ImgRht src={listli} alt="" />
                                <ImgRht src={Line2} alt="" />
                            </div>
                            <SubTit>Energy recovery speed</SubTit>
                            <Speed><span>{item.attr.Recover_Speed}</span></Speed>
                            <SubTit>Number of equipment</SubTit>
                            <Speed><span>{item.attr.Slots}</span></Speed>
                            <LastRow>
                                <LftPrice>
                                    <img src={PriceImg} alt=""/>
                                    <Price>{item.price} <span>{item.symbol}</span></Price>
                                </LftPrice>
                            </LastRow>
                        </Lirht>

                    </li>
                ))
            }


        </Ul>
        </Content>

}
