
const getBalance = async (contractInstance,account) =>{

    const result = await contractInstance.balanceOf(account);
    return  result.toString()
}

const tokenOfOwnerByIndex = async (contractInstance,account,index) =>{

    const result = await contractInstance.tokenOfOwnerByIndex(account,index);
    return  result
}

const tokenURI = async (contractInstance,id) =>{

    const result = await contractInstance.tokenURI(id);
    console.error("====tokenURI==",result)
    return  result
}

export default {
    getBalance,
    tokenURI,
    tokenOfOwnerByIndex
};
