import { ethers } from "ethers";
import abi from "../abi/Fighter";

let loadMain = false;
let Mock24Contract;

const { mockX24 } = window.GeneralConfig;

export default async function Mock24Connect(state, dispatch) {


    const {web3InstanceState, Mock24ContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            Mock24Contract = new ethers.Contract(mockX24, abi, web3Instance);
            dispatch({type: 'SET_MOCKX24', payload: Mock24Contract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MOCKX24_ERROR'});
        }
    };
    if (Mock24ContractState !== 'LOAD_MOCKX24') return;
    if (loadMain) return dispatch({type: 'SET_MOCKX24', payload: Mock24Contract});
    loadMain = true;
    asyncLoadMain();
}
