import styled from "styled-components";
import {useWeb3} from "../api/contracts";

import errorBg from "../assets/images/error/errorBg.png";
import errorImg from "../assets/images/error/errorimg.png";
import errorLine from "../assets/images/error/errorLine.png";
import btn120 from "../assets/images/btn120.png";
import ErrorBg from "../assets/images/mobile/mobileError.png";
import ErrorLineM from "../assets/images/mobile/errorLineMobile.png";


const Mask = styled('div')`
  background:url(${errorBg}) no-repeat;
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 9999999;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-content: center;
   @media (max-width: 1000px) {
      background:url(${ErrorBg}) no-repeat;
      background-size: 100% 100%;
    }

`;

const  Mid = styled('div')`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`

const Btn = styled('button')`
      background: url(${btn120}) no-repeat;
    background-size: 100% 100%;
    width: 1.2rem;
    height: 0.44rem;
    margin: 0 auto 0.64rem;
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    color: #0C0C0B;
    line-height: 0.44rem;
    cursor: pointer;
    border: 0;
    
    @media (max-width: 1000px) {
      width: 163px;
      height: 44px;
      font-size: 14px;
      margin-bottom: 64px;
    }
`

const Line1 = styled('div')`
  margin-bottom: 0.62rem;
  img{
    width: 7.76rem;
    height: 0.32rem;
  }
  .pc{
    display: block;
  }
  .mobile{
    display: none;
  }
  @media (max-width: 1000px) {
  margin-bottom: 64px;
      background:url(${ErrorBg}) no-repeat;
      background-size: 100% 100%;
      .pc{
        display: none;
      }
      .mobile{
        display: block;
      }
      img{
        width: 100%;
        height: auto;
      }
    }
`

const Line2 = styled(Line1)`
  margin-bottom: 0;
   @media (max-width: 1000px) {
      background:url(${ErrorBg}) no-repeat;
      background-size: 100% 100%;
      .pc{
        display: none;
      }
      .mobile{
        display: block;
      }
      img{
        width: 100%;
        height: auto;
      }
    }
`

const ErrorImgBg = styled('div')`
  margin-bottom: 0.48rem;
  text-align: center;
   @media (max-width: 1000px) {
      img{
        width: 90%;
        height: auto;
      }
    }
  
`;

const Content = styled('div')`
  margin-bottom: 0.32rem;
  width: 4.89rem;
    font-size: 0.14rem;
      font-family: 'orbitron-regular';
    font-weight: 400;
    color: #FFFAF2;
    line-height: 0.24rem;
    text-align: center;
    @media (max-width: 1000px) {
      font-size: 12px;
      font-family: 'orbitron-regular';
      line-height: 2em;
      width: 90%;
      margin-bottom: 24px;
      }
`

export default function ErrorModal() {

    const {  dispatch, state } = useWeb3();
    const {  errorTips } = state;

    const CloseModal = () =>{
        dispatch({type: 'SET_ERROR',payload: null });
    }

    return <div>
        {
            errorTips !=null && <Mask>
                <Mid>
                    <Line1>
                        <img className="pc" src={errorLine} alt=""/>
                        <img className="mobile" src={ErrorLineM} alt=""/>
                    </Line1>
                    <ErrorImgBg>
                        <img src={errorImg} alt=""/>
                    </ErrorImgBg>
                    <Content>{ errorTips.msg || 'Sorry, your operation cannot be performed at this time, please troubleshoot the problem and try again.'}</Content>
                    {
                        !errorTips.btn &&<Btn onClick={()=>CloseModal()}>Confirm</Btn>
                    }

                    <Line2>
                        <img className="pc" src={errorLine} alt=""/>
                        <img className="mobile" src={ErrorLineM} alt=""/>
                    </Line2>
                </Mid>

            </Mask>
        }
        </div>

}
