import { ethers } from "ethers";
import abi from "../abi/Fighter";

let loadMain = false;
let Mock29Contract;

const { mockX29 } = window.GeneralConfig;

export default async function Mock29Connect(state, dispatch) {

    const {web3InstanceState, Mock29ContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            Mock29Contract = new ethers.Contract(mockX29, abi, web3Instance);
            dispatch({type: 'SET_MOCKX29', payload: Mock29Contract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MOCKX29_ERROR'});
        }
    };
    if (Mock29ContractState !== 'LOAD_MOCKX29') return;
    if (loadMain) return dispatch({type: 'SET_MOCKX29', payload: Mock29Contract});
    loadMain = true;
    asyncLoadMain();
}
