import React, { useState} from "react";
import styled from "styled-components";

import navLine from "../assets/images/nav_hover.png";
// import Status from  '../assets/images/status.png';

const NavDiv = styled.ul`
   display: flex;
   justify-content: center;
   width: 100%;
    li {
        cursor: pointer;
        position: relative;
        line-height: .65rem;
        margin:0.2rem 0 0  .80rem;
        color: rgba(255, 249, 242, 0.3);
 
        //text-transform: uppercase;
        &:first-child {
            margin-left: 0;
        }
        &.selected {
            color: #FF9400;
            font-weight: bold;
        }
        .line {
          display: block;
            position: absolute;
            top: .3rem;
            left: 0;
            width: 100%;
            text-align: center;
            .nor{
            opacity: 0.6;
            }
        }
    }
    
   @media (max-width: 1000px) {
      padding-bottom: 15px;
    li {

        line-height: .72rem;
        font-size: 14px;
        .line {
            top: 20px;
        }
      }
    }
`;


export default function Navlist(props) {
    let [curNavIndex, setCurNavIndex] = useState(0);

    const clickNav = (navIndex) => {

        if(props.home){
            setCurNavIndex(0);
        }else{
            setCurNavIndex(navIndex);
        }


        props.handleSelect(navIndex);
    }
    const navData = props.list;
    let navItems = [];
    for (let i = 0; i<navData.length; i++) {
        navItems.push(
            <li
                className={( curNavIndex === i) ? 'selected' : ''}
                onClick={()=>clickNav(i)}
                key={i}>
                {navData[i].name}
                <div className="line" >
                    {
                        curNavIndex === i && <img src={navLine} alt=""/>
                    }
                    {/*{*/}
                    {/*    curNavIndex !== i && !props.home && <img src={Status} className="nor" alt=""/>*/}
                    {/*}*/}

                </div>
            </li>
        )
    }
    return  <NavDiv>
        {navItems}
    </NavDiv>
}
