const reducer = (state, action) => {
    switch (action.type) {
        //web3Instance
        case 'CONNECT_INIT':
            return { ...state, web3InstanceState: 'CONNECT_INIT' };

        case 'CONNECT':
            return { ...state, web3Instance: action.payload, web3InstanceState: 'READY' };

        case 'CONNECT_ERROR':
            return { ...state, web3InstanceState: 'ERROR', web3InstanceError: action.payload };


        //accounts
        case 'LOAD_ALLACCOUNT':
            return { ...state, accountState: 'LOAD_ALLACCOUNTS', Account:null};

        case 'SET_ALLACCOUNT':
            return { ...state, Account: action.payload, accountState: 'READY' };

        case 'ALLACCOUNT_ERROR':
            return { ...state, Account: null, accountState: 'ERROR' };

        case 'SHOW_PAGE':
            return { ...state, showpage: action.payload};

        case 'SHOW_MODAL':
            return { ...state, showModalAct: action.payload};

        //main contract
        case 'LOAD_MAINCONTRACT':
            return { ...state, NFTcontractState: 'LOAD_MAINCONTRACT' };

        case 'SET_MAINCONTRACT':
            return { ...state, NFTcontract: action.payload, NFTcontractState: 'READY' };

        case 'MAINCONTRACT_ERROR':
            return { ...state, NFTcontract: null, NFTcontractState: 'ERROR' };

            //IRon Contract
        case 'LOAD_IRON':
            return { ...state, IRonContractState: 'LOAD_IRON' };

        case 'SET_IRON':
            return { ...state, IRonContract: action.payload, IRonContractState: 'READY' };

        case 'IRON_ERROR':
            return { ...state, IRonContract: null, IRonContractState: 'ERROR' };

            //Mock21 Contract
        case 'LOAD_MOCKX21':
            return { ...state, Mock21ContractState: 'LOAD_MOCKX21' };

        case 'SET_MOCKX21':
            return { ...state, Mock21Contract: action.payload, Mock21ContractState: 'READY' };

        case 'MOCKX21_ERROR':
            return { ...state, Mock21Contract: null, Mock21ContractState: 'ERROR' };



            //Mock24 Contract
        case 'LOAD_MOCKX24':
            return { ...state, Mock24ContractState: 'LOAD_MOCKX24' };

        case 'SET_MOCKX24':
            return { ...state, Mock24Contract: action.payload, Mock24ContractState: 'READY' };

        case 'MOCKX24_ERROR':
            return { ...state, Mock24Contract: null, Mock24ContractState: 'ERROR' };


            //Mock27 Contract
        case 'LOAD_MOCKX27':
            return { ...state, Mock27ContractState: 'LOAD_MOCKX27' };

        case 'SET_MOCKX27':
            return { ...state, Mock27Contract: action.payload, Mock27ContractState: 'READY' };

        case 'MOCKX27_ERROR':
            return { ...state, Mock27Contract: null, Mock27ContractState: 'ERROR' };



            //Mock29 Contract
        case 'LOAD_MOCKX29':
            return { ...state, Mock29ContractState: 'LOAD_MOCKX29' };

        case 'SET_MOCKX29':
            return { ...state, Mock29Contract: action.payload, Mock29ContractState: 'READY' };

        case 'MOCKX29_ERROR':
            return { ...state, Mock29Contract: null, Mock29ContractState: 'ERROR' };


        case 'SET_LOADING':
            return { ...state, loading: action.payload };

        case 'SET_ERROR':
            return { ...state, errorTips: action.payload };

        case 'SHOW_NAV':
            return { ...state, navigatation: action.payload };


        default:
            throw new Error(`Unknown type: ${action.type}`);
    }
};
export default reducer
