import React, {useState} from "react";

import MarketFoot from "./market/marketfoot";

import {useWeb3} from "../api/contracts";
import styled from "styled-components";
import Accounts from "../api/Account";
import {useNavigate} from "react-router-dom";

import logo from "../assets/images/logo.png";
import wallet from "../assets/images/mobile/wallet.png";
import walletAft from "../assets/images/mobile/walletAFt.png";
import close from "../assets/images/iconClose.png";
import arrowrht from "../assets/images/arrowrht.png";
import arrowlft from "../assets/images/arrowlft.png";
import Plane from "../assets/images/plane2.png";

import mobileModal from "../assets/images/mobile/mobileModal.jpg"


const Imgbg = styled('div')`
  text-align: right;
  height: 200px;
  position: relative;
  
   img{
      position: absolute;
      right: 0;
      bottom: 0;
    }
`
const Toplist = styled('div')`
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
`;

const NavBg = styled('div')`
    background: rgba(0,0,0,0.9);
    background: url(${mobileModal}) no-repeat;
    background-size: 100% 100%;
    width: 100vw;
    //height: calc( 100vh - 40px);
    height: 100vh;
    left: 0;
    //top: 1.8rem;
    top:0;
    z-index: 99;
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
   justify-content:space-between;
   overflow: hidden;
`;


const Ul = styled('ul')`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    li{
      width: 100%;
      text-align: center;
       text-transform: uppercase;
       font-size: 14px;
        margin-bottom: 48px;
       flex-grow: 0;
      opacity: 0.6;
      &.active{
        font-weight: bold;
        color: #FF9400;
        opacity: 1;
      }
    }
    span{
      padding: 0 17px;
    }
    
`;

const FooterBg = styled('div')`
      @media (min-width: 1000px) {
        display: none;
    }
`


const Logo = styled.div`
  padding-top: .1rem;
  img {
       width: 1.47rem;
    }
    @media (max-width: 1000px) {
     img{
     width: 79px;
     }
    }
    
`;

const RhtIcon = styled('div')`
    font-size: 10px;
    display: flex;
    align-items: center;
    &:first-child{
      margin-right:16px;
    }
    
    @media (min-width: 1000px) {
        display: none;
    }
`;

const NavTop = styled('div')`
  display: flex;
  padding: 8px 20px;
  justify-content: space-between;
`

const RhtTop = styled('div')`
  display: flex;
    .after{
  margin-right: 8px;
  }
`

export default function NavMobile() {

    const {  dispatch, state } = useWeb3();
    const { navigatation,Account } = state;
    const navigate = useNavigate();
    const [list] = useState([{name:'Home'},{name:'Market'},{name:'WhiterPaper'},{name:'My iFighter'}]);
    const [current] = useState(0);

    const handleSelect = (navIndex) => {
        // setCurNavIndex(navIndex);
        // let url;
        closeNavBg()
        switch (navIndex) {
            case 1:
                // navigate('/market/1');
                dispatch({type: 'SHOW_MODAL',payload: true});
                break;
            case 2:
                // url = '/market/1';
                window.open("https://wiki.ifighterinfinity.io/");
                navigate('/');
                break;
            case 3:
                navigate('/myifighter/1');
                // dispatch({type: 'SHOW_MODAL',payload: true});
                break;
            default:
                navigate('/');
                break;
        }
    }

    // const showNavBg = () =>{
    //     document.body.parentNode.style.overflowY ="hidden";
    //     dispatch({type: 'SHOW_NAV',payload: true})
    //
    // }
    const closeNavBg = () =>{
        document.body.parentNode.style.overflowY ="auto";
        dispatch({type: 'SHOW_NAV',payload: null})

    }

    const AddresstoShow = (address) => {

        let frontStr = address.substring(0, 4);

        let afterStr = address.substring(address.length - 4, address.length);

        return `${frontStr}****${afterStr}`

    }


    const connect = async() => {
        const accoutlist = await Accounts.account();
        if(accoutlist.type === "success"){
            dispatch({type: 'SET_ALLACCOUNT',payload:accoutlist.data});
            sessionStorage.setItem('account',accoutlist.data);
        } else {
            dispatch({type: 'SET_ERROR',payload: {msg:"Please install MetaMask"}});
        }
    }
    return <div>
        {
            navigatation && <NavBg>
                <NavTop>
                    <Logo>
                        <img src={logo} alt="" />
                    </Logo>
                    <RhtTop>
                        {
                            Account==null &&<RhtIcon onClick={()=>connect()}>
                                <img src={wallet} alt=""/>
                            </RhtIcon>
                        }
                        {
                            Account!=null &&<RhtIcon onClick={()=>connect()}>
                                <img src={walletAft} alt="" className="after"/>
                                {AddresstoShow(Account)}
                            </RhtIcon>
                        }

                        {
                            <RhtIcon onClick={()=>closeNavBg()}>
                                <img src={close} alt=""/>
                            </RhtIcon>
                        }
                    </RhtTop>
                </NavTop>
                <Toplist>
                    <Ul>
                        {
                            list.map((item,index)=>(
                                <li className={current === index ? "active":0} key={`navlist_${index}`} onClick={()=>handleSelect(index)}>
                                    {
                                        current === index &&    <img src={arrowrht} alt=""/>
                                    }
                                    <span>{item.name}</span>
                                    {
                                        current === index &&    <img src={arrowlft} alt=""/>
                                    }
                                </li>
                            ))
                        }

                    </Ul>
                    <Imgbg>
                        <img src={Plane} alt=""/>
                    </Imgbg>

                </Toplist>

                <FooterBg>
                    <MarketFoot />
                </FooterBg>
            </NavBg>
        }

    </div>
}
