export default function MintBox() {
    const navigate = useNavigate();
    const { type } = useParams();

    const {dispatch,state} = useWeb3();
    const { Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract,Account} = state;

    const [NFTobj, setNFTobj] = useState(null);

    // const getIfighterInfo = async(contract) =>{
    //     dispatch({type: 'SET_LOADING',payload:true});
    //
    //     let totalfighter = await mockx.getBalance(contract,Account);
    //     const total = totalfighter && BigNumber.from(totalfighter).toNumber();
    //
    //     const rsId = mockx.tokenOfOwnerByIndex(contract,Account,total-1);
    //     let result = await mockx.tokenURI(contract,rsId);
    //     console.log("====tokenURI=====",result);
    //
    //     dispatch({type: 'SET_LOADING',payload:null});
    // }
    useEffect(() => {
        dispatch({ type: 'LOAD_MAINCONTRACT' });
        dispatch({ type: 'LOAD_MOCKX29' });
        dispatch({ type: 'LOAD_MOCKX21' });
        dispatch({ type: 'LOAD_MOCKX24' });
        dispatch({ type: 'LOAD_MOCKX27' });
        dispatch({ type: 'LOAD_IRON' });
    }, []);

    const FormatJson = (arrObj) =>{

            let attr={}
        arrObj.attributes.map(p=> attr[p.trait_type]=p.value)
        arrObj.attr = attr;

        return arrObj

    }
    useEffect(() => {
        if(Mock29Contract == null || Mock21Contract == null || Mock24Contract == null || Mock27Contract == null || Account == null)return;
        const { mockX21,mockX24,mockX27,mockX29 } = window.GeneralConfig;
        let obj;
        switch (type) {
            case mockX21:
                obj =FormatJson(myX21) ;
               // obj = getIfighterInfo(Mock21Contract)
                break;
            case mockX24:
                obj = FormatJson(myX24);
                // obj = getIfighterInfo(Mock24Contract)
                break;
            case mockX27:
                obj = FormatJson(myX27);
                // obj = getIfighterInfo(Mock27Contract)
                break;
            case mockX29:
                obj = FormatJson(myX29);
                // obj = getIfighterInfo(Mock29Contract)
                break;
            default:break;

        }
        setNFTobj(obj)
    }, [type,Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract,Account]);

    const ToUrl = (url) => {
        if(!url){
            navigate('/')
        }else{
            navigate( url )
        }

    }
    const Confirm = async() =>{
        ToUrl(`/mint`)
    }
    return <MintBg>
        <MintHeader>
            <Header />
        </MintHeader>
        <NavMobile />
        <SubNavBox>  <SubNav  title="Mint"/></SubNavBox>
        <Main>
            {
                NFTobj!=null &&<Contents>
                    <Lilft>
                        <Imgs src={NFTobj.image} alt=""/>
                    </Lilft>
                    <Lirht>
                        <Titles>{NFTobj.name}</Titles>
                        <ImgRht src={listli} alt="" />
                        <SubTit>iGas consumption factor</SubTit>
                        <Speed>{NFTobj.attr.iGAS_Factor}</Speed>
                        <SubTit>Number of complimentary iRon</SubTit>
                        <Speed><span>{NFTobj.attr.iRON_Factor}</span></Speed>
                        <SubTit>Rarity</SubTit>
                        <StarV>
                            <Star value={NFTobj.attr.Rarity}/>
                        </StarV>
                        <RhtA onClick={()=>Confirm()} >Confirm</RhtA>
                    </Lirht>
                    <FooterBg>
                    <MarketFoot />
                </FooterBg>
                </Contents>
            }
        </Main>
    </MintBg>
}
