import { ethers } from "ethers";
import abi from "../abi/Fighter";

let loadMain = false;
let Mock21Contract;

const { mockX21 } = window.GeneralConfig;

export default async function Mock21Connect(state, dispatch) {

    const {web3InstanceState, Mock21ContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            Mock21Contract = new ethers.Contract(mockX21, abi, web3Instance);
            dispatch({type: 'SET_MOCKX21', payload: Mock21Contract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MOCKX21_ERROR'});
        }
    };
    if (Mock21ContractState !== 'LOAD_MOCKX21') return;
    if (loadMain) return dispatch({type: 'SET_MOCKX21', payload: Mock21Contract});
    loadMain = true;
    asyncLoadMain();
}
