import { ethers } from "ethers";
import abi from "../abi/MockERC20";

let loadMain = false;
let IRonContract;

const { ironToken } = window.GeneralConfig;

export default async function IRonConnect(state, dispatch) {

    const {web3InstanceState, IRonContractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const LoadIRon = () => {

        try {
            IRonContract = new ethers.Contract(ironToken, abi, web3Instance);
            dispatch({type: 'SET_IRON', payload: IRonContract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'IRON_ERROR'});
        }
    };
    if (IRonContractState !== 'LOAD_IRON') return;
    if (loadMain) return dispatch({type: 'SET_IRON', payload: IRonContract});
    loadMain = true;
    LoadIRon();
}
