import React, {useState,useEffect} from "react";

import {useWeb3} from "../../api/contracts";
import styled from "styled-components";

import Modal from "../modal";

import Edit from "../../assets/images/edit.png";
import btn120 from "../../assets/images/btn120.png";
import Navbg from "../../assets/images/navBg.png";
import confirmBg from "../../assets/images/confirmBg.png";
import max from "../../assets/images/max.png";

const Bg = styled('div')`
  width: 4.4rem;
  height: 1rem;
  background: url(${confirmBg});
  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 0.24rem 0.32rem 0.16rem 0.24rem;
  text-align: left;
`;

const Tips = styled('div')`
    font-size: 0.14rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color: rgba(255, 249, 242, 0.6);
    line-height: 0.24rem;
    margin-bottom: 0.08rem;
`;

const ButtonGroup = styled('div')`
  margin-top: 0.5rem;
  button{
    width: 1.2rem;
    height: 0.44rem;
    margin-right: 0.32rem;
    border: 0;
    cursor: pointer;
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    line-height: 0.44rem;

  }
`;

const BtnBuy = styled('button')`
    background: url(${btn120});
    background-size: 100% 100%;
`;

const BtnCancel = styled('button')`
    background: url(${Navbg});
    background-size: 100% 100%;
    color:rgba(255, 249, 242, 0.3);
`;

const Contents = styled('div')`
  display:flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
   span{
      padding-left: 0.08rem;
      font-family: 'orbitron-regular';
        font-size: 0.16rem;
        font-weight: 400;
        line-height: 0.28rem;
    }
  input{
        font-size: 0.2rem;
        font-family: "Orbitron";
        font-weight: bold;
        line-height: 0.28rem;
        //flex-grow: 1;
        background: transparent!important;
        border: 0;
        color: #FFF9F2;
        &:focus{
          outline: none;
        }
    }
`;

const Max = styled('div')`
     position: absolute;
     right: -0.6rem;
     top: 0 ;
     background: url(${max}) no-repeat;
     background-size: 100% 100%;
     width: 0.48rem;
     height: 0.24rem;
     text-align: center;
     line-height: 0.24rem;
   font-family: 'orbitron-regular';
    font-size: 0.12rem;
    font-weight: 400;
    color: #0C0C0B;
    text-transform: capitalize;
`

export default function BuyIgas(props) {

    const {  dispatch, state } = useWeb3();
    const { showModalAct } = state;
    const [ igas, setIGas] = useState(0);

    const handleclose = () =>{
        dispatch({type: 'SHOW_MODAL',payload:null});
    }

    const handleChange = (e) =>{
        setIGas(e.target.value)
    }

    useEffect(()=>{
        setIGas(props.itemvalue)
    },[props.itemvalue])

    return  <Modal show={showModalAct} >
        <div>
            <Bg>
                <Tips>Please confirm your purchase quantity</Tips>
                <Contents>
                    <input type="text" value={igas} onChange={handleChange}/>
                    <span>iGas</span>
                    <div><img src={Edit} alt=""/></div>
                    <Max>max</Max>
                </Contents>
            </Bg>
            <ButtonGroup>
                <BtnBuy>Buy</BtnBuy>
                <BtnCancel onClick={()=>handleclose()}>Cancel</BtnCancel>
            </ButtonGroup>
        </div>
    </Modal>
}
