import React, { useState, useEffect} from "react";
import styled from 'styled-components';

import {useWeb3} from "../../api/contracts";

import Bg from '../../assets/images/diamond.png';
import connect from '../../assets/images/btn120.png';
import SwitchIcon from "../../assets/images/switch.png";
import SelectArrow from '../../assets/images/selectArrow.png';
import SwitctLine from '../../assets/images/switchLine.png';

const Bgwidth =styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
`;

const DiamondBg = styled.div`
     background: url(${Bg}) no-repeat;
     background-size: 100% 100%;
     width: 4.4rem;
     height: 4.4rem;
     padding: 0.32rem 0.24rem;
     box-sizing: border-box;
`;

const FirstRow = styled.div`
  //border-bottom: 1px solid #FFF9F2;
  padding-bottom: 0.12rem;
  background: url(${SwitctLine}) no-repeat left bottom;
  background-size: 100%;
`;

const SubTitles = styled.div`
    font-size: 0.12rem;
    font-weight: 400;
    line-height: 0.24rem;
    margin-bottom: 0.16rem;
`;

const Num = styled.input`
    font-size: 0.2rem;
    font-family: "Orbitron";
    font-weight: bold;
    line-height: 0.32rem;
    background: transparent!important;
    border: 0;
    width: 80%;
    color: #FFF9F2;
    &:focus{
      outline: none;
    }
`;

const Selected = styled.div`
  display:flex;
  align-items: center;
  justify-content: space-between;
`;

const Switch = styled.div`
  min-height: 0.2rem;
  margin: 0.36rem 0 0.28rem;
  cursor: pointer;
`;

const Btn = styled.button`
    width: 1.2rem;
    height: 0.4rem;
    background:url(${connect}) no-repeat;
    background-size: 100% 100%;
    border: 0;
    margin-top: 0.64rem;
    cursor: pointer;
    
    
font-size: 0.16rem;
 font-family: "Orbitron-Medium";
font-weight: 500;
color: #0C0C0B;
`;

const SelectBg = styled('div')`
display: flex;
    justify-content: flex-end;
    width: 20%;
 
  select{
    display: flex;
    font-family: 'orbitron-regular';
    border: 0;
    font-size: 0.12rem;
    font-weight: 400;
    color: #FFF9F2;
    line-height: 2em;
    appearance:none;  
    -moz-appearance:none;  
    -webkit-appearance:none;  
    background: url(${SelectArrow}) no-repeat right center !important;  
    padding-right: 20px;
    text-align: right;
    &:focus{
      outline: none;
    }
  }
`

export default function Diamond() {

    const {  dispatch } = useWeb3();

    let [fromValue, setFromValue] = useState('');
    let [fromSymbol, setFromSymbol] = useState('USDT');
    let [toValue, setToValue] = useState('');
    let [toSymbol, setToSymbol] = useState('IIS');


    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: null });
    },[]);

    const changeTokens = () =>{
        setFromValue(toValue);
        setToValue(fromValue);

        setFromSymbol(toSymbol);
        setToSymbol(fromSymbol);

    }

    const handleChange = (e) =>{
        const { name ,value} = e.target;
        switch (name) {
            case 'from':
                setFromValue(value);
                break;
            case 'fromSymbol':
                setFromSymbol(value);
                break;
            case 'to':
                setToValue(value);
                break;
            case 'toSymbol':
                setToSymbol(value);
                break;
            default:
                break;
        }
    }
    const confirm = () =>{
        let obj ={
            fromValue,
            fromSymbol,
            toValue,
            toSymbol
        }
        console.log("=====",obj)
    }

    return <Bgwidth>
            <DiamondBg>
                <FirstRow>
                    <SubTitles>From</SubTitles>
                    <Selected>
                        <Num type="text" value={fromValue} name="from" onChange={handleChange}/>
                        <SelectBg>
                            <select value={fromSymbol} name="fromSymbol" onChange={handleChange}>
                                <option value="USDT">USDT</option>
                                <option value="IIS">IIS</option>
                            </select>
                        </SelectBg>
                    </Selected>
                </FirstRow>
                <Switch onClick={()=>changeTokens()}>
                    <img src={SwitchIcon} alt=""/>
                </Switch>
                <FirstRow>
                    <SubTitles>To</SubTitles>
                    <Selected>
                        <Num type="text" value={toValue} name="to" onChange={handleChange} />
                        <SelectBg>
                            <select value={toSymbol} name="toSymbol" onChange={handleChange}>
                                <option value="USDT">USDT</option>
                                <option value="IIS">IIS</option>
                            </select>
                        </SelectBg>
                    </Selected>
                </FirstRow>
                <div>
                    <Btn onClick={()=>confirm()}>Buy</Btn>
                </div>
            </DiamondBg>
        </Bgwidth>;
}
