import Arrow from "../../assets/images/arrowTo.png";
import SaleTop from "../../assets/images/saleTop.png";
import {useWeb3} from "../../api/contracts";
import Erc20 from "../../api/iron";


const Ul = styled('ul')`
  margin:0 0 0.64rem -0.24rem;
  display: flex;
  justify-content: center;
  li{
    margin-left: 0.24rem;
    width: 2.46rem;
    height: 1.52rem;
    background: url(${SaleTop}) no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    padding: 0.32rem 0.24rem 0.12rem;
  }
`

const Imgs = styled('img')`
  min-width: 0.2rem;
  height: 0.16rem;
  margin-bottom: 0.16rem;
`

const Imgbtn = styled('img')`
  width: 0.24rem;
  height: 0.24rem;
  margin-top: 0.08rem;
`;

const Titles = styled('div')`
    font-size: 0.12rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    color:rgba(255, 249, 242, 0.6);
    line-height: 0.2rem;
    margin-bottom: 0.04rem;
`

export default function Tokenlist() {

    const {  dispatch,state } = useWeb3();
    const { IRonContract,Account } = state;
    const [total, setTotal] = useState(0);

    useEffect(() => {
        if(IRonContract == null || Account == null)return;
        dispatch({type: 'SET_LOADING',payload:true});
        const initTime = async () => {
            let lft = await Erc20.getBalance(IRonContract,Account);
            dispatch({type: 'SET_LOADING',payload:null});
            setTotal(lft)
        }

        initTime();

    }, [IRonContract,Account]);


    return <Ul>
        {/*<li>*/}
        {/*    <Imgs src={Glod} alt=""/>*/}
        {/*    <Titles>iGlod</Titles>*/}
        {/*    <div>987,654,321,000.00</div>*/}
        {/*    <Imgbtn src={Arrow} alt=""/>*/}
        {/*</li>*/}
        {/*<li>*/}
        {/*    <Imgs src={IIs} alt=""/>*/}
        {/*    <Titles>IIS</Titles>*/}
        {/*    <div>987,654,321,000.00</div>*/}
        {/*    <Imgbtn src={Arrow} alt=""/>*/}
        {/*</li>*/}
        <li>
            <Imgs src={Iron} alt=""/>
            <Titles>iRon</Titles>
            <div>{total}</div>
            <Imgbtn src={Arrow} alt=""/>
        </li>
        {/*<li>*/}
        {/*    <Imgs src={Igas} alt=""/>*/}
        {/*    <Titles>iGas</Titles>*/}
        {/*    <div>987,654,321,000.00</div>*/}
        {/*    <Imgbtn src={Arrow} alt=""/>*/}
        {/*</li>*/}
    </Ul>
}
