import React, { useState } from "react";
import styled from 'styled-components';

import Equiplist from "./equiplist";
import Navbg from "../../assets/images/navBg.png"
import NavbgAct from "../../assets/images/navBgAct.png"
import LineRht from "../../assets/images/lineRht.png";


const Equipment = styled.div`
width: 100%;
height: 100%;
`;

const NavBrdr = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  //margin: -0.2rem 0 0.48rem;
  border-bottom: 1px solid rgba(255, 249, 242, 0.3);
  padding-bottom: 0.32rem;
  img{
    width: 1.2rem;
    height: 0.08rem;
  }

  ul{
    justify-content: flex-start;
    display: flex;
  }
  li{
    text-transform: capitalize;
    height: 0.44rem;
    background: url(${Navbg}) no-repeat;
    background-size: 100% 100%;
    text-align: center;
    line-height: 0.44rem;
    font-size: 0.12rem;
    padding: 0 0.25rem;
    margin-right: 0.32rem;
    opacity: 0.3;
    cursor: pointer;
    &.active{
     background: url(${NavbgAct}) no-repeat;
      background-size: 100% 100%;
      color: #FF9400;
      opacity: 1;
    }
  }
`;

export default function Equip() {

    const [list] = useState([{name:'weapons'}, {name:'Armours'},{name:'reaction devices'}]);
    const [curIndex, setCurIndex] = useState(0);

    const handleSelect = (index) =>{
        setCurIndex(index)
    }
    return <Equipment>
        <NavBrdr>
            <ul>
                {
                    list.map((item,index)=>(
                        <li className={curIndex ===index ?'active':''} onClick={()=>handleSelect(index)}><span>{item.name}</span></li>
                    ))
                }
            </ul>
            <img src={LineRht} alt=""/>
        </NavBrdr>
        {
            curIndex === 0 && <Equiplist />
        }

    </Equipment>
}
