import React, { useState,useEffect} from "react";
import styled from 'styled-components';
import {useNavigate, useParams} from 'react-router-dom';
import {useWeb3} from "./api/contracts";
import Reback from "./components/reBack";

import MarketFoot from './components/market/marketfoot';
import MyAircraft from "./components/myifighter/myaircraft";
import Equip from "./components/myifighter/equip";
import Token from "./components/myifighter/token";

import LftBg from './assets/images/myLft.png';
import liBg from './assets/images/tag.png';
import liActive from  './assets/images/tagNOr.png';
import AllBg from './assets/images/marketBg.jpg';
import LoadingModal from "./components/loadingModal";
import ErrorModal from "./components/ErrorModal";
import SubNav from "./components/subNav";



const AppBg = styled.div`
  overflow: hidden;
  background:url(${AllBg}) no-repeat;
  background-size: 100% 100%;
  min-height:  100vh;
  
`;
const MainBrdr = styled.div`
  padding-top: 0.48rem ;
  box-sizing: border-box;
  width: 13.44rem;
  min-height: 100vh;
  margin: 0 auto;
`;

const TopMain = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: stretch;
  height: 100%;
`;

const Lft = styled.div`
   min-width: 20%;
  ul{
    background: url(${LftBg}) no-repeat;
    background-size: 100% 100%;

      height: calc(100vh - 1.45rem);
        
      padding: 0.6rem ;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
  }
  li{
    background: url(${liActive}) no-repeat;
    background-size: 100% 100%;
    width: 1.6rem;
    height: 0.56rem;
    line-height: 0.64rem;
    text-transform: capitalize;
    text-align: center;
    margin: 0.24rem 0;
    cursor: pointer;
    span{
      opacity: 0.6;
    }
     &.active{  
      background: url(${liBg}) no-repeat;
      background-size: 100% 100%;
     font-weight: 500;
      color: #0C0C0B;
       font-family: "Orbitron-Medium";
        span{
        opacity: 1;
        }
    }
  }
         @media (max-width: 1000px) {
     display: none;
    }
`;

const RhtTop = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  flex-grow: 1;
  
`;
const Rht = styled.div`
  width: 100%;
  padding-left: 0.36rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
`;

const SubNavBox  = styled('div')`
  display: none;
   @media (max-width: 1000px) {
     display: flex;
     justify-content: space-between;
     align-items: center;
    }
`



export default function MyIfighter() {
    const { id } = useParams();
    const navigate = useNavigate();
    const {  dispatch, state } = useWeb3();
    const { web3Instance} = state;
    const [active, setActive] = useState(1);
    const [navList] = useState(["Warship","equipment","token"])

    useEffect(()=>{
        setActive(parseInt(id))
    },[id])


    useEffect(()=>{
        const initProvider = async() =>{
            const { ethereum } = window;

            if(typeof ethereum == 'undefined'){
                dispatch({type: 'SET_ERROR',payload: {msg:"Please install MetaMask"}});
            }else{
                window.ethereum.on('chainChanged', () => {
                    window.location.reload()
                });
                window.ethereum.on('accountsChanged', function (arr) {
                    dispatch({type: 'SET_ALLACCOUNT',payload:null});
                    sessionStorage.removeItem('account');
                    window.location.reload()
                });
                chainChange();

            }
        }

        initProvider();
    },[])

    useEffect(() => {
        dispatch({ type: 'LOAD_MAINCONTRACT' });
        dispatch({ type: 'LOAD_MOCKX29' });
        dispatch({ type: 'LOAD_MOCKX21' });
        dispatch({ type: 'LOAD_MOCKX24' });
        dispatch({ type: 'LOAD_MOCKX27' });
        dispatch({ type: 'LOAD_IRON' });
    }, []);
    useEffect(() => {
        if(web3Instance == null )return

        let selectedStorage = sessionStorage.getItem('account');
        if (selectedStorage) {
            dispatch({type: 'SET_ALLACCOUNT',payload:selectedStorage});
        }
    }, [web3Instance]);

    const handleSelect = (index) =>{
        setActive(index)
        navigate(`/myifighter/${index}`)
    }

    const chainChange = () =>{
        const { chainName,nativeCurrency:{name,symbol,decimals},rpcUrls,blockExplorerUrls,onlineChainId } = window.GeneralConfig;
        window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [{
                chainId:onlineChainId,
                chainName,
                nativeCurrency: {
                    name,
                    symbol,
                    decimals
                },
                rpcUrls,
                blockExplorerUrls
            }]
        })
            .catch((error) => {
                dispatch({type: 'SET_ERROR',payload:{msg:'Please take the right chain',btn:true}  });
            })
    }
    return <AppBg>
        <LoadingModal  />
        <ErrorModal  />
        <MainBrdr>
            <TopMain>
                <Lft>
                    <ul>
                        <li className={active ===1 ?'active':''} onClick={()=>handleSelect(1)}><span>Warship</span></li>
                        {/*<li className={active ===2 ?'active':''} onClick={()=>handleSelect(2)}><span>equipment</span></li>*/}
                        <li className={active ===3 ?'active':''} onClick={()=>handleSelect(3)}><span>Token</span></li>
                    </ul>
                    <Reback />
                </Lft>
                <Rht>
                    <SubNavBox>  <SubNav title={navList[active-1]} select={true} navList={navList} my={true}/></SubNavBox>
                    <RhtTop>
                        {
                            active ===1 && <MyAircraft />
                        }
                        {
                            active ===2 && <Equip />
                        }
                        {
                            active ===3 && <Token />
                        }
                    </RhtTop>

                    <MarketFoot />
                </Rht>
            </TopMain>

        </MainBrdr>
    </AppBg>

}
