import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {useNavigate} from "react-router-dom";
import {useWeb3} from "../api/contracts";
import Accounts from "../api/Account";
import logo from "../assets/images/logo.png";
import Back from "../assets/images/back.png";
import Arrow from "../assets/images/mobile/arrowBtm.png";
import wallet from "../assets/images/mobile/wallet.png";
import Navi from "../assets/images/navigation.png";
import walletAft from "../assets/images/mobile/walletAFt.png";

import selectBg from "../assets/images/mobile/selectBg.png";
import arrowrht from "../assets/images/arrowrht.png";
import arrowlft from "../assets/images/arrowlft.png";

const TopBox = styled('div')`
width: 100%;
`

const NavSub  = styled('div')`
  font-size: 20px;
  margin-bottom: 16px;
  width: 100%;
  .lft{
    display: flex;
    align-items: center;
    span{
      margin-right: 16px;
    }
    img{
    width: 79px;
    height: 28px;
    }
  }
  .backArr{
    width: 24px!important;
    height: 24px!important;
  }
  .rotate{
    transform:rotate(180deg);
  }
  .rht{
    display: flex;
    img:last-child{
      margin-left: 10px;
    }
    &>div{
     display: flex;
    align-items: center;
    }
  }
   .spanRht{
      font-size: 10px!important;
      margin-left: 10px;
    }
   @media (max-width: 1000px) {
     display: flex;
     justify-content: space-between;
     align-items: center;
    }
`

const SelectBox = styled('div')`
    position: absolute;
    width: 200px;
    height: ${ props => props.showbg ?'180px':'284px!important' };
    background: url(${selectBg}) no-repeat;
    background-size: 100% 100%;
    top: 50px;
    z-index: 99;
    padding: 26px 0;
    box-sizing: border-box;
    ul{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
    }
    li{
      text-align: center;
      font-size: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      img{
        padding: 0 16px;
      }
    }
    .active{
    color: #FF9500; 
    }
`

export default function SubNav(props) {
    const {  dispatch, state } = useWeb3();
    const { web3Instance,Account } = state;
    const navigate = useNavigate();

    const [show,setShow] = useState(false);

    useEffect(() => {
        if(web3Instance == null )return

        let selectedStorage = sessionStorage.getItem('account');
        if (selectedStorage) {
            dispatch({type: 'SET_ALLACCOUNT',payload:selectedStorage});
        }
    }, [web3Instance]);

    const ToUrl = (url) => {
        if(!url){
            navigate('/')
        }else{
            navigate(url)
        }
    }
    const handleSelect = () =>{
        if(!props.select) return;
        setShow(!show)
    }

    const handleTOgo = (index) =>{
        navigate(`/market/${index+1}`)
        setShow(false)
    }

    const AddresstoShow = (address) => {

        let frontStr = address.substring(0, 4);

        let afterStr = address.substring(address.length - 4, address.length);

        return `${frontStr}****${afterStr}`

    }

    const connect = async() => {
        const accoutlist = await Accounts.account();

        if(accoutlist.type === "success"){
            dispatch({type: 'SET_ALLACCOUNT',payload:accoutlist.data});
            sessionStorage.setItem('account',accoutlist.data);
        } else {
            dispatch({type: 'SET_ERROR',payload: {msg:"Please install MetaMask"}});
        }
    }
    const showNavBg = () =>{
        document.body.parentNode.style.overflowY ="hidden";
        dispatch({type: 'SHOW_NAV',payload: true})

    }

    return <TopBox>
        <NavSub>
            {
               !props.showleft && <div className="lft">
                    <img src={Back} alt=""  className="backArr" onClick={()=>ToUrl()}/>
                    <span onClick={()=>handleSelect(props.select)}>{props.title}</span>
                   {
                       props.select && <img src={Arrow} className={ !show ?'backArr':'backArr rotate'} alt="" />
                   }

                </div>
            }
            {
               props.showleft && <div className="lft">
                   <img src={logo} alt="" />
                </div>
            }
            {
                show &&<SelectBox showbg={props.my}>

                    <ul>
                        {
                            props.navList && props.navList.map((item,index)=>(<li key={`nav_${index}`}  className={props.title ===item ?'active':''} onClick={()=>handleTOgo(index)}>
                                    {
                                        props.title ===item &&    <img src={arrowrht} alt=""/>
                                    }
                                    <span>{item}</span>
                                    {
                                        props.title ===item &&    <img src={arrowlft} alt=""/>
                                    }
                                </li>
                            ))
                        }
                    </ul>
                </SelectBox>
            }


            <div className="rht">
                {
                    Account !=null &&<div>
                        <img src={walletAft} alt="" className="after"/>
                        <span className="spanRht">{AddresstoShow(Account)}</span>
                    </div>
                }
                {
                    Account ==null &&<div onClick={()=>connect()}>
                        <img src={wallet} alt=""/>
                    </div>

                }
                <div onClick={()=>showNavBg()}>
                    <img src={Navi} alt=""/>
                </div>
            </div>
        </NavSub>
    </TopBox>
}
