export default function Aircraft() {

    const {  dispatch } = useWeb3();
    const [activeItem, setActiveItem] = useState(null);

    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: true });
    },[]);

    const buyAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
    }

    return <div>
        <ConfirmModal item={activeItem} tips="Please confirm if you need to purchase this aircraft ." />
        <Ul>
            {
                aircraftlist && aircraftlist.map((item,index)=>(<li key={`aircraft_${index}`}>
                    <Imgs src={item.img} alt=""/>
                    <Titles>{item.name}</Titles>
                    <div>
                        <ImgRht src={listli} alt="" />
                        <ImgRht src={Line2} alt="" />
                    </div>

                    <SubTit>Energy recovery speed</SubTit>
                    <Speed><span>{item.speed}</span></Speed>
                    <SubTit>Number of equipment</SubTit>
                    <Speed><span>{item.equipment}</span></Speed>
                    <SubTit>User</SubTit>
                    <Speed><span>{item.user}</span></Speed>
                    <LastRow>
                        <LftPrice>
                            <img src={PriceImg} alt=""/>
                            <Price>{item.price} <span>{item.symbol}</span></Price>
                        </LftPrice>
                    </LastRow>
                    <RhtA onClick={()=>buyAircraft(item)}>buy</RhtA>
                </li>))
            }
        </Ul>
        </div>

}
