import { ethers } from "ethers";
import abi from "../abi/NFTMaster";

let loadMain = false;
let NFTcontract;

const { nftMaster } = window.GeneralConfig;

export default async function mainConnect(state, dispatch) {

    const {web3InstanceState, NFTcontractState,web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            NFTcontract = new ethers.Contract(nftMaster, abi, web3Instance);
            dispatch({type: 'SET_MAINCONTRACT', payload: NFTcontract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MAINCONTRACT_ERROR'});
        }
    };
    if (NFTcontractState !== 'LOAD_MAINCONTRACT') return;
    if (loadMain) return dispatch({type: 'SET_MAINCONTRACT', payload: NFTcontract});
    loadMain = true;
    asyncLoadMain();
}
