import React, { useState,useEffect} from "react";
import styled from 'styled-components';
import {useNavigate, useParams} from 'react-router-dom';

import Reback from "./components/reBack";

import MarketFoot from './components/market/marketfoot';
import Aircraft from "./components/market/aircraft";
import Equip from "./components/market/equip";
import IGas from "./components/market/igas";
import Diamond from "./components/market/diamond";
import IIS from "./components/market/iis";
import SubNav from "./components/subNav";

import LftBg from './assets/images/LftBg.png';
import liBg from './assets/images/tag.png';
import liActive from  './assets/images/tagNOr.png';
import AllBg from './assets/images/mobile/mobileAircraft.png';



const AppBg = styled.div`
  overflow: hidden;
  background:url(${AllBg}) no-repeat;
  background-size: 100% 100%;
  min-height:  100vh;
  
`;
const MainBrdr = styled.div`
  padding-top: 0.48rem ;
  box-sizing: border-box;
  width: 13.44rem;
  min-height: 100vh;
  margin: 0 auto;
`;

const TopMain = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: stretch;
  height: 100%;
`;

const Lft = styled.div`
   min-width: 20%;
  ul{
    background: url(${LftBg}) no-repeat;
    background-size: 100% 100%;

      height: calc(100vh - 1.45rem);
        
      padding: 0.6rem ;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
  }
  li{
    background: url(${liActive}) no-repeat;
    background-size: 100% 100%;
    width: 1.6rem;
    height: 0.56rem;
    line-height: 0.64rem;
    text-transform: capitalize;
    text-align: center;
    margin: 0.24rem 0;
    cursor: pointer;
    span{
      opacity: 0.6;
    }
     &.active{  
      background: url(${liBg}) no-repeat;
      background-size: 100% 100%;
     font-weight: 500;
      color: #0C0C0B;
       font-family: "Orbitron-Medium";
        span{
        opacity: 1;
        }
    }
  }

    @media (max-width: 1000px) {
      display: none;
    }
`;

const RhtTop = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  flex-grow: 1;
     @media (max-width: 1000px) {
         align-items: flex-start;
    }
`;
const Rht = styled.div`
  width: 100%;
  padding-left: 0.36rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const SubNavBox  = styled('div')`
  display: none;
   @media (max-width: 1000px) {
     display: flex;
     justify-content: space-between;
     align-items: center;
    }
`


export default function Market() {
    const { id } = useParams();
    const navigate = useNavigate();

    const [active, setActive] = useState(1);
    const [navList] = useState(["Warship","equipment","iGas Energy","iGold","IIS"])

    useEffect(()=>{

        setActive(parseInt(id))
    },[id])

    const handleSelect = (index) =>{
        setActive(index)
        navigate(`/market/${index}`)
    }
    return <AppBg>
        <MainBrdr>
            <TopMain>
                <Lft>
                    <ul>
                        {
                            navList.map((item,index)=>(
                                <li key={`market_${index}`} className={active ===index+1 ?'active':''} onClick={()=>handleSelect(index+1)}><span>{item}</span></li>))
                        }
                    </ul>
                    <Reback />
                </Lft>
                <Rht>
                    <SubNavBox>  <SubNav title={navList[active-1]} select={true} navList={navList} my={false}/></SubNavBox>
                    <RhtTop>
                        {
                            active ===1 && <Aircraft />
                        }
                        {
                            active ===2 && <Equip />
                        }
                        {
                            active ===3 && <IGas />
                        }
                        {
                            active ===4 && <Diamond />
                        }
                        {
                            active ===5 && <IIS />
                        }
                    </RhtTop>

                    <MarketFoot />
                </Rht>
            </TopMain>

        </MainBrdr>
        </AppBg>

}
