import React, {useEffect} from 'react';
import styled from 'styled-components';

import {useWeb3} from "../api/contracts";
import MarketFoot from "./market/marketfoot";
import SubNav from "./subNav";

import maskbg from '../assets/images/modal/mask.png';
import mask60 from '../assets/images/modal/mask60.png';
import bg from '../assets/images/modal/bg.png';
import Mobilebg from '../assets/images/mobile/mobileModal.jpg';

const Mask = styled('div')`
     background: url(${maskbg}) no-repeat;
     background-size: 100% 100%;
     position: fixed;
     top: 0;
     left: 0;
     width: 100vw;
     height: 100vh;
     z-index: 99;
     opacity: ${ props => props.opacity || 1 };
`;

const Bg = styled('div')`
    background:url(${props => props.bg ? bg : mask60});
    background-size:100% 100% ;
    
    
     width: 100vw;
     height: 100vh;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     @media (max-width: 1000px) {
        justify-content: space-between;
        background:url(${Mobilebg}) no-repeat;
          background-size: 100% 100%;
    }
`
const Middle = styled('div')`
    min-width: 2.2rem;
    text-align: center;
    @media (max-width: 1000px) {
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
    }
`

const FooterBg = styled('div')`
      @media (min-width: 1000px) {
        display: none;
    }
`

const SubNavBox  = styled('div')`
  display: none;
   @media (max-width: 1000px) {
     display: flex;
     
     justify-content: flex-end;
     align-items: center;
     width: 100%;
     padding: 10px 20px 0 ;
     box-sizing: border-box;
    }
`

export default function Modal(props) {
    const { dispatch, state } = useWeb3();
    const { showModalAct } = state;
    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: null });
    },[]);
    return<div>
        {
            showModalAct!=null && <Mask>
                <Bg bg={props.bg}>
                    <SubNavBox>  <SubNav showleft={true}/></SubNavBox>
                    <Middle>
                        {props.children}
                    </Middle>

                    <FooterBg>
                        {
                            !props.noFoot &&<MarketFoot />
                        }

                    </FooterBg>
                </Bg>
            </Mask>
        }
    </div>
}
