import styled from "styled-components";

import StarAll from "../assets/images/starAll.png";
import StarHalf from "../assets/images/starHalf.png";
import StarEmpty from "../assets/images/starEmpty.png";

const StarBg = styled.div`
  display: flex;

`

export default function Star(props) {

    return <StarBg>
        {[...Array( parseInt(props.value))].map((e, i) => {
            return <img key={i} src={StarAll} alt=""/>
        })}
        {
            props.value % 1 >0  && <img src={StarHalf} alt=""/>
        }
        {[...Array( (5 - (props.value).toFixed(0)))].map((e, i) => {
            return <img key={i} src={StarEmpty} alt=""/>
        })}


    </StarBg>
}
