import React, { useEffect } from 'react';

import styled from 'styled-components';

import mintBg from "../assets/images/MintBg.png";
import {useWeb3} from "../api/contracts";

const Mask = styled('div')`

  background:url(${mintBg}) no-repeat;
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 9999999;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-content: center;


`

const Loader = styled('div')`
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100%;
    -webkit-animation: top 3s ease-in;
    animation: top 3s ease-in;
    z-index: 2;
    
    .loader__bar {
    opacity: .6;
    content: "";
    display: inline;
    position: absolute;
    height: 6px;
    z-index: 999999999;
    text-align: center;
}

  .loader__bar#bar1 {
    background-color: #FF9500;
    -webkit-animation: loadingbar 2s ease-in infinite;
    animation: loadingbar 2s ease-in infinite
}

    .loader__bar#bar2 {
    background-color: rgba(255, 250, 242, 0.3);
    -webkit-animation: loadingbar 2s ease-in 1s infinite;
    animation: loadingbar 2s ease-in 1s infinite
}

.loader__text-holder {
    width: 100%;
    display: block;
    height: 50px;
    text-align: center;
}

.loader__text {
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    display: block;
    //position: absolute;
    margin: 0 auto;
    width: 100%;
    //top: 0;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important
}

.loader__text--header {
    opacity: .3;
}
@-webkit-keyframes loadingbar {
    from {
        left: 50%;
        width: 0;
        z-index: 100
    }
    33.3333% {
        left: 0;
        width: 100%;
        z-index: 10
    }
    to {
        left: 0;
        width: 100%
    }
}

@keyframes loadingbar {
    from {
        left: 50%;
        width: 0;
        z-index: 100
    }
    33.3333% {
        left: 0;
        width: 100%;
        z-index: 10
    }
    to {
        left: 0;
        width: 100%
    }
}
`;

const LoaderMid = styled('div')`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
    position: relative;
    .box {
   position: absolute;
   text-align: center;
   //top:calc(60% - 80px);
   top:60%;
   left:cacl(50% - 200px);
        width: 200px;
        height: 160px;
        line-height: 40px;
        overflow: hidden;
        li{
          opacity: 0.2;
        }
    }
`
export default function LoadingModal() {
    const { state } = useWeb3();
    const { loading } = state;

    useEffect(()=>{
        if(loading == null )return;
        var box = document.getElementById('scrollbox')
        var parent = document.getElementsByTagName('ul')[0]
        var parent2 = document.getElementsByTagName('ul')[1]
        parent2.innerHTML = parent.innerHTML

        var timer = setInterval(autoScrollLine, 30)
        function autoScrollLine() {
            if (box.scrollTop >= parent.offsetHeight) {
                box.scrollTop = 0;
            } else {
                box.scrollTop++;
            }
            if (box.scrollTop % (box.offsetHeight/4) === 0) {

                let n =  box.scrollTop / 40
                document.getElementsByTagName('li')[n].style.opacity=1

                clearInterval(timer)
                setTimeout(() => {
                    if( document.getElementsByTagName('li')[n]){
                        document.getElementsByTagName('li')[n].style.opacity= 0.2
                    }

                    timer = setInterval(autoScrollLine, 30)
                }, 2000)
            }
        }
    },[loading])

    return  <div>
        {
            loading != null &&<Mask>
                <Loader>
                    <LoaderMid>
                        <div>
                            <div className="loader__text-holder">
                                <div className="loader__text loader__text--header">Please wait a moment, iFighter NTF is minting
                                </div>
                            </div>
                            <div className="loader__bar" id="bar1" />
                            <div className="loader__bar" id="bar2" />
                        </div>

                        <div id="scrollbox" className="box" >
                            <ul>
                                <li>X-24</li>
                                <li>X-21</li>
                                <li>X-27</li>
                                <li>X-29</li>
                                <li>X-24</li>
                                <li>X-21</li>
                                <li>X-27</li>
                                <li>X-29</li>
                            </ul>
                            <ul></ul>
                        </div>
                    </LoaderMid>
                </Loader>
            </Mask>
        }
    </div>
}
