export default function Mint() {
    const navigate = useNavigate();
    const {dispatch,state} = useWeb3();
    const { NFTcontract,web3Instance, Account} = state;

    const [hourTime, sethourTime] = useState(0);
    const [minuteTime, setMinuteTime] = useState(0);
    const [secondTime, setSecondTime] = useState(0);
    const [leftTime, setLeftTime] = useState(0);
    const [Remain, setRemain] = useState(0);

    const ToUrl = (url) => {
        if(!url){
            navigate('/')
        }else{
            navigate( url )
        }

    }



    useEffect(() => {
        if(NFTcontract == null)return;

        let timer = setInterval(()=>{
            switchTime();
        },1000);
        return () => {
            clearInterval(timer);
            timer = null
        }
    }, [NFTcontract,leftTime]);

    useEffect(() => {
        if(NFTcontract == null || web3Instance == null)return;

        const initTime = async () => {
           let lft = await nft.GetInfo(NFTcontract)
            setLeftTime(lft.saleTimeAt.toString())
            setRemain(lft.size.toString() - lft.soldCount.toString())
        }
        initTime();
    }, [NFTcontract,web3Instance]);

    const switchTime = () => {
        const currentTime = leftTime * 1000 - Date.parse(new Date());

        if(currentTime>0){

            const hours = parseInt(currentTime/1000 / 60 / 60 % 24);
            const minutes = parseInt(currentTime/1000 / 60 % 60);
            const seconds = parseInt(currentTime/1000 % 60);
            sethourTime(hours);
            setMinuteTime(minutes);
            setSecondTime(seconds);
        }
    }

    const buymint = async() =>{
        if(Account == null){
            dispatch({type: 'SET_ERROR',payload:{msg:''}});
            return
        }
        dispatch({type: 'SET_LOADING',payload:true});
         await nft.drawBoxes(NFTcontract,Account,web3Instance).then((data)=>{
            let type = data[0].address;
             setTimeout(()=>{
                 dispatch({type: 'SET_LOADING',payload:null});
                 ToUrl(`/mintbox/${type}`)
             },0)
        }).catch((error)=>{
             dispatch({type: 'SET_LOADING',payload:null});
             console.error(error)
             dispatch({type: 'SET_ERROR',payload:{msg:error && error.data&&error.data.message ?error.data.message  :error.message} });
         })

        // setTimeout(()=>{
        //     dispatch({type: 'SET_LOADING',payload:null});
        //     ToUrl(`/mintbox`)
        // },10000)

    }
    const { price } = window.GeneralConfig;
    return <MintBg>
        <MintHeader>
            <Header />
        </MintHeader>
        <NavMobile />
        <SubNavBox>  <SubNav  title="Mint"/></SubNavBox>
        <Main>
            <div>
                <MintBrdr>
                    <MintTop>
                        <div>
                            <MintTitle>Mint your Ifighter</MintTitle>
                            <Num>{Remain}</Num>
                            <TipsTop>Each mystery box contain one warship (level may differ) with certain amount of IRON.</TipsTop>
                            <Arrow>
                                <img src={MintArrow} alt=""/>
                                <img src={MintArrow} alt=""/>
                                <img src={MintArrow} alt=""/>
                            </Arrow>
                            <ButtonRht> <Price><img src={Icon} alt=""/>{price} BNB</Price><button onClick={()=>buymint()} /></ButtonRht>
                            <TipsBtm>Each wallet address can be minted a total of 5 times</TipsBtm>
                        </div>

                    </MintTop>
                    <MintBtm>
                        <CountTit>Countdown</CountTit>
                        <CountDown>
                            <div className="number">{hourTime > 10 ? parseInt(hourTime/10):0}</div>
                            <div className="number">{hourTime > 10 ? hourTime % 10 : hourTime}</div>
                            <span>:</span>
                            <div className="number">{minuteTime > 10 ? parseInt(minuteTime/10):0}</div>
                            <div className="number">{minuteTime > 10 ? minuteTime % 10 : minuteTime}</div>
                            <span>:</span>
                            <div className="number">{secondTime > 10 ? parseInt(secondTime/10):0}</div>
                            <div className="number">{secondTime > 10 ? secondTime % 10 : secondTime}</div>
                        </CountDown>
                    </MintBtm>
                </MintBrdr>
                <Back onClick={() =>ToUrl()}> <img src={backimg} alt=""/>home</Back>
                <FooterBg>
                    <MarketFoot />
                </FooterBg>
            </div>
            <Plane />
        </Main>
    </MintBg>
}
